import tTable from '@/components/t-table/t-table.vue';
import tTh from '@/components/t-table/t-th.vue';
import tTr from '@/components/t-table/t-tr.vue';
import tTd from '@/components/t-table/t-td.vue';
export default {
	components: {
		tTable,
		tTh,
		tTr,
		tTd
	},
	data() {
		return {
			tableList: [{
					id: 0,
					name: '张三',
					age: '19',
					hobby: '游泳'
				},
				{
					id: 1,
					name: '李四',
					age: '21',
					hobby: '绘画'
				},
				
			],
			resultData:{},
			table1:[],
			table2:[],
			table3:[],
			id:''
		};
	},
	onLoad(option) {
		this.id =  option.id;
		/* 基础信息 */
		this.initData(option.id);
		
	},
	methods:{
		initData(id){
			this.$api.getByWorkId({
				id:id
			}).then(res=>{
				this.resultData  = res.data.data
			});
			/* 人资费用 */
			this.$api.getByWorkOrderIdPerson({
				workOrderId:id
			}).then(res=>{
				this.table1 = res.data.data
			});
			/* 设备费用 */
			this.$api.getByWorkOrderIdEqu({
				workOrderId:id
			}).then(res=>{
				this.table2 = res.data.data
			});
			/* 物料信息 */
			this.$api.getByWorkOrderIdSup({
				workOrderId:id
			}).then(res=>{
				this.table3 = res.data.data
			});
		}
			
		
	}
}
